   <p class="section-description" :style="[eyeClick === true ? {'font-size': BigFontSize} : {'font-size': smallFontSize}]">
      Українці вважають корупцію поширеною передусім у найвищих органах влади. Першість тут займають Парламент, Кабінет Міністрів, і Президент, причому саме на останнього вони покладають найбільшу відповідальність за подолання корупції. </p>
    <p class="section-description" :style="[eyeClick === true ? {'font-size': BigFontSize} : {'font-size': smallFontSize}]">  
     Органи відповідальні за подолання корупції, на думку громадян не мають бажання її долати. І навпаки – хоча неурядовий сектор, на думку громадян, прагне долати корупцію, але він не сприймається як відповідальний за цю роботу. </p>
    <p class="section-description" :style="[eyeClick === true ? {'font-size': BigFontSize} : {'font-size': smallFontSize}]">  
     Нові антикорупційні органи сприймаються громадянами найкраще порівняно з усіма іншими центральними органами влади: їхні показники сприйняття корумпованості є найнижчими, а віра в їхнє бажання подолати корупцію – найвища поміж державних інституцій. </p>     
     <p class="section-description" :style="[eyeClick === true ? {'font-size': BigFontSize} : {'font-size': smallFontSize}]">
     Ті хто готові долучитися до антикорупційної діяльності, менш схильні вважати, що корупція дуже поширена в органах влади.</p>

     <p id="hyperlink" style="margin:50px 0; font-size:13px; font-style:italic;">*  Оскільки сфери та органи влади змінювалися між першим і останнім опитуванням, для забезпечення можливості порівняння деякі сфери та органи влади були ототожнені. Зокрема, так було зроблено щодо національної поліції та прокуратури, хоча в минулих опитуваннях в деяких питаннях міліція фігурувала разом із прокуратурою.</p> 
  </section>



