 <footer>
      <button id="methodology" @click="open = !open">Методологія дослідження</button>
      <div v-show="open" :style="[eyeClick === true ? {'font-size': BigFontSize, 'margin-bottom': '50px' } : {'font-size': smallFontSize, 'margin-bottom': '50px'}]">
        <p>У візуалізації використані дані 5-х досліджень громадської думки про стан корупції в Україні, що проводилися з 2007 до 2018 року. Вибірка, розроблена для кожного з п’яти досліджень, є репрезентативною для дорослого населення (віком 18 років і старшим) України в цілому, а також для кожної області України. Дослідження проводились за випадковою багатоступінчастою вибіркою з квотним відбором на останньому етапі. На першому етапі формування вибірки в межах кожної області випадковим чином були відібрані населені пункти пропорційно до кількості населення в них. Другий етап передбачав випадковий відбір виборчих дільниць (у 2007 - 2011 роках – поштових відділень) на території відібраних населених пунктів. На території кожної з відібраних дільниць випадковим чином відбирались вулиці, будинки та квартири. Останнім етапом був відбір респондента в межах домогосподарства і безпосереднє проведення опитування. Отримані дані зіставлялись із даними про статево-вікову структуру населення за даними Державної служби статистики.</p> 
        <p>Загальна вибірка кожного дослідження перевищую 10 тис. осіб. Теоретична похибка досліджень складає 1% для загальнонаціональної вибірки. </p> 
        <p>Через те, що в полах питання про досвід корупції були сформульовані по-різному, за досвід корупції визначається згідно з питаннями про усі сфери.</p> 
        <p>Оскільки сфери та органи влади змінювалися між першим і останнім опитуванням, для забезпечення можливості порівняння деякі сфери та органи влади були ототожнені. Зокрема, так було зроблено щодо національної поліції та прокуратури, хоча в минулих опитуваннях в деяких питаннях міліція фігурувала разом із прокуратурою.</p> 
        
        <table>
          <thead>            
            <th>Питання</th>
            <th>2018</th>
            <th>2015</th>
            <th>2011</th>
            <th>2009</th>
            <th>2007</th>
          </thead>
          <tbody>
            <tr>
              <td>На Вашу думку, хто відповідальний за подолання корупції в Україні?</td>
              <td>Національна поліція</td>
              <td>Міліція та спеціалізовані правоохоронні органи для боротьби з корупцією <i>(в переліку також були присутні НАБУ, НАЗК, СБУ)</i></td>
              <td>Міліція та прокуратура</td>
              <td>Міліція та прокуратура</td>
              <td>Міліція та прокуратура</td>
            </tr>
