              <td>…Національній поліції (включаючи патрульну поліцію)?</td>
              <td>…в міліції (без ДАІ)?</td>
              <td>…в міліції (без ДАІ)?</td>
              <td>…в міліції (без ДАІ)?</td>
              <td>…в міліції (без ДАІ)?</td>
            </tr>

            <tr>
              <td>На Вашу думку, хто відповідальний за подолання корупції в Україні?</td>
