              <td>Прокуратурі</td>
              <td>Прокуратурі</td>
              <td>Прокуратурі</td>
              <td>Прокуратурі</td>
              <td>Прокуратурі</td>
            </tr>
          </tbody>
        </table>
        <p>Перелік сфер дещо змінювався, а саме: в 2018 році додалася сфера системи державних закупівель, якої не було в попередніх опитуваннях, зате зникла сфера ДАІ, функції якої були ширшими за функції сервісних центрів МВС. Для підрахування досвіду корупції були обрані тільки ті питання, що відображали функції ДАІ, які перебрали сервісні центр МВС (отримання водійських прав, проведення технічного огляду, реєстрація автомобіля). Через те, що питання «Чи стикалися ви зі ДАІ?» стосувалося ширшого переліку функції ліквідованого органу, рахувати показники від тих, хто стикався зі сферою, було б некоректним.</p>

        <p>Вважалося, що респондент мав досвід корупції у більшості зі сфер, якщо він ствердно відповідав на 3 питання щодо сфери:</p>
        <ol>
          <li>Чи вимагали від Вас або членів Вашої родини хабаря (грошей, подарунка, протекції, послуги) під час звернень?</li>
          <li>Чи пропонували Ви або члени Вашої родини самі хабаря (гроші, подарунки, протекцію, послуги) для отримання послуг під час звернень?</li>
          <li>Чи використовували особисті зв’язки  для отримання послуг?</li>
        </ol>
       <p>Утім, в опитування 2007 року були задані тільки перші 2 питання з 3, тому врахувати досвід використання особистих зв’язків у цьому опитуванні немає можливості.</p> 
       <p>У сферах охорони здоров’я, шкільної освіти та ДАІ (в опитуваннях до 2015 року включно) був заданий інший перелік питань, із специфікацією можливого корупційного досвіду, та іншим переліком відповідей.  Вважалося, що респондент має корупційний досвід, якщо його примушували дати хабар або придбати дещо, або ж якщо він добровільно давав хабар, аби отримати потрібний результат, навіть якщо його не змушували (наприклад, при проведенні технічного огляду). У разі якщо за відсутності примусу матеріальні втрати з боку отримувача послуг можна витлумачити як добровільну благодійну допомогу (наприклад, купівля інструментів і ліків в лікарнях, внески у фонд школи чи класу), вважалося, що респондент мав досвід корупції лише якщо його змушували це робити.</p>
       <p>Регіональні показники показувалися, якщо показник показувався від сукупності не менше 20 респондентів.</p>
       <p>Вважалося, що респондент готовий долучитися до антикорупційної боротьби, якщо він заявляв про готовність долучитися до хоча б однієї з запропонованих активностей:</p>
       <ul>
          <li>Брати участь у громадських радах або громадських слуханнях для нагляду за державними установами та їхніми рішеннями </li>
          <li>Ініціювати та підписувати петиції до місцевих, обласних та національних органів влади</li>
          <li>Повідомляти про корупцію засобам масової інформації, в тому числі в соціальних мережах, для привернення уваги і тиску</li>
          <li>Звертатися до народних депутатів для привернення уваги і тиску</li>
          <li>Повідомляти про випадки корупції правоохоронним органам (НАБУ, поліція) та НАЗК</li>
          <li>Подавати скарги на випадки корупції до державних органів, у яких вона спостерігалася</li>
          <li>Брати участь у мітингах та публічних протестах</li>
          <li>Підтримувати антикорупційні громадські організації та ініціативи та брати участь в їх діяльності</li>          
       </ul>
