 та квартири. Останнім етапом був відбір респондента в межах домогосподарства і безпосереднє проведення опитування. Отримані дані зіставлялись із даними про статево-вікову структуру населення за даними Державної служби статистики.")]),_c('p',[_vm._v("Загальна вибірка кожного дослідження перевищую 10 тис. осіб. Теоретична похибка досліджень складає 1% для загальнонаціональної вибірки. ")]),_c('p',[_vm._v("Через те, що в полах питання про досвід корупції були сформульовані по-різному, за досвід корупції визначається згідно з питаннями про усі сфери.")]),_c('p',[_vm._v("Оскільки сфери та органи влади змінювалися між першим і останнім опитуванням, для забезпечення можливості порівняння деякі сфери та органи влади були ототожнені. Зокрема, так було зроблено щодо національної поліції та прокуратури, хоча в минулих опитуваннях в деяких питаннях міліція фігурувала разом із прокуратурою.")]),_vm._m(16),_c('p',[_vm._v("Перелік сфер дещо змінювався, а саме: в 2018 році додалася сфера системи державних закупівель, якої не було в попередніх опитуваннях, зате зникла сфера ДАІ, функції якої були ширшими за функції сервісних центрів МВС. Для підрахування досвіду корупції були обрані тільки ті питання, що відображали функції ДАІ, які перебрали сервісні центр МВС (отримання водійських прав, проведення технічного огляду, реєстрація автомобіля). Через те, що питання «Чи стикалися ви зі ДАІ?» стосувалося ширшого переліку функції ліквідованого органу, рахувати показники від тих, хто стикався зі сферою, було б некоректним.")]),_c('p',[_vm._v("Вважалося, що респондент мав досвід корупції у більшості зі сфер, якщо він ствердно відповідав на 3 питання щодо сфери:")]),_vm._m(17),_c('p',[_vm._v("Утім, в опитування 2007 року були задані тільки перші 2 питання з 3, тому врахувати досвід використання особистих зв’язків у цьому опитуванні немає можливості.")]),_c('p',[_vm._v("У сферах охорони здоров’я, шкільної освіти та ДАІ (в опитуваннях до 2015 року включно) був заданий інший перелік питань, із специфікацією можливого корупційного досвіду, та іншим переліком відповідей.  Вважалося, що респондент має корупційний досвід, якщо його примушували дати хабар або придбати дещо, або ж якщо він добровільно давав хабар, аби отримати потрібний результат, навіть якщо його не змушували (наприклад, при проведенні технічного огляду). У разі якщо за відсутності примусу матеріальні втрати з боку отримувача послуг можна витлумачити як добровільну благодійну допомогу (наприклад, купівля інструментів і ліків в лікарнях, внески у фонд школи чи класу), вважалося, що респондент мав досвід корупції лише якщо його змушували це робити.")]),_c('p',[_vm._v("Регіональні показники показувалися, якщо показник показувався від сукупності не менше 20 респондентів.")]),_c('p',[_vm._v("Вважалося, що респондент готовий долучитися до антикорупційної боротьби, якщо він заявляв про готовність долучитися до хоча б однієї з запропонованих активностей:")]),_vm._m(18)])])]),_vm._m(19),_vm._m(20)])}
